import { Component, Host, Prop, State, Watch, h } from '@stencil/core';

/**
 * The dot navigation component used to indicate the current index in a list of views.
 */
@Component({
  tag: 'bs-dot-navigation',
  styleUrl: 'dot-navigation.scss',
  shadow: true,
})
export class BsDotNavigation {
  private timeout: NodeJS.Timeout;

  private interval: NodeJS.Timeout;

  @State() progress: number = 0;

  /**
   * The amount of dots in the navigation.
   */
  @Prop() items!: number;

  /**
   * The active dot index.
   */
  @Prop() index!: number;

  @Watch('index')
  handleIndexChange() {
    this.startProgress();
  }

  /**
   * Should the auto navigation be slow?
   */
  @Prop() slow?: boolean;

  /** Methods */
  private startProgress = () => {
    let time = 0;

    clearTimeout(this.timeout);
    clearInterval(this.interval);

    this.interval = setInterval(() => {
      time += 100;

      this.progress = (100 / (this.slow ? 30 : 15)) * (time / 1000);
    }, 100);

    this.timeout = setTimeout(
      () => {
        clearInterval(this.interval);
      },
      this.slow ? 30000 : 15000
    );
  };

  /** lifecycle hooks */
  componentDidLoad() {
    this.startProgress();
  }

  render() {
    const dots = Array.from(Array(this.items).keys());

    return (
      <Host class={this.slow ? 'is-slow' : ''}>
        <span class="dot-nav">
          {dots.map((index) =>
            index === this.index ? (
              <progress class="dot dot--active" max="100" value={this.progress} />
            ) : (
              <progress class="dot" max="100" value="0" />
            )
          )}
        </span>
      </Host>
    );
  }
}
